
import { Form, Field, ErrorMessage, useForm } from 'vee-validate'
import { useToken, useUser } from '../service/UserStore'
import { onMounted, defineComponent } from 'vue'
import router from '../router/index'

export default defineComponent({
  components: {
    Form,
    Field,
    ErrorMessage
  },
  setup() {
    const { handleSubmit } = useForm()
    const onSubmit = handleSubmit((values) => {
      alert(JSON.stringify(values, null, 2))
    })

    function validateEmail(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(value)) {
        return 'Keine gültige E-Mail-Adresse!'
      }
      return true
    }
    function validateField(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      return true
    }
    function validateNumber(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      if (isNaN(Number(value))) {
        return 'Keine gültige Nummer!'
      }
      return true
    }
    const { jwtDecrypt } = useToken()
    const { profileUser, actUserProfile, edit } = useUser()
    const user = actUserProfile
    const name = jwtDecrypt().sub

    onMounted(async () => {
      await profileUser(name)
    })

    // Profil des Nutzers wird bearbeitet
    function editU(
      gender: string,
      username: string,
      surname: string,
      name: string,
      street: string,
      adressnr: number,
      city: string,
      zipcode: number,
      email: string
    ) {
      edit(
        gender,
        username,
        surname,
        name,
        street,
        adressnr,
        city,
        zipcode,
        email
      )
      router.go(0)
    }
    return {
      onSubmit,
      validateEmail,
      validateField,
      validateNumber,
      user,
      editU
    }
  }
})
