
import { useUser } from '../service/UserStore'
import { Form, Field, ErrorMessage as ErrorMsg } from 'vee-validate'
import { ref } from 'vue'

export default {
  components: {
    Form,
    Field,
    ErrorMsg
  },
  setup() {
    const { register, errormessage } = useUser()

    const errMsg = errormessage
    const gender = ref('')
    const username = ref('')
    const surname = ref('')
    const name = ref('')
    const street = ref('')
    const addressnr = ref()
    const city = ref('')
    const zipcode = ref()
    const email = ref('')
    const password = ref('')
    const passwordWdh = ref('')

    function onSubmit() {
      register(
        gender.value,
        username.value,
        surname.value,
        name.value,
        street.value,
        addressnr.value,
        city.value,
        zipcode.value,
        email.value,
        password.value
      )
    }

    function validateUser(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      return true
    }

    const emailRegEx = new RegExp(
      /^(\w-*)+(.(\w-*)+)*@([a-zA-Z0-9][a-zA-Z0-9-]*[a-zA-Z0-9]+)(.[A-Za-z]{2,})$/
    )

    function validateEmail(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      if (!emailRegEx.test(value)) {
        return 'Keine gültige E-Mail-Adresse!'
      }
      return true
    }

    const passwordRegEx = new RegExp(/^(?=.*[A-Z])(?=.*[\W|_])[\w\W]{6,}$/)

    function validatePassword(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      if (!passwordRegEx.test(value)) {
        return 'Mind. ein Großbuchstabe, ein Sonderzeichen und insg. 6 Zeichen!'
      }
      return true
    }
    function validateEqual(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      if (password.value !== passwordWdh.value) {
        return 'Passwörter stimmen nicht überein!'
      }

      return true
    }
    function validateField(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      return true
    }
    function validateNumber(value: string) {
      if (!value) {
        return 'Pflichtfeld, bitte ausfüllen!'
      }
      if (isNaN(Number(value))) {
        return 'Keine gültige Nummer!'
      }
      return true
    }

    return {
      register,
      errMsg,
      onSubmit,
      validateEmail,
      validatePassword,
      validateField,
      validateNumber,
      validateEqual,
      validateUser,
      password,
      passwordWdh,
      username,
      name,
      surname,
      email,
      street,
      addressnr,
      gender,
      city,
      zipcode
    }
  }
}
